(function() {
  angular.module('neurology-clinic').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $stateProvider.state('home', {
      url: '/',
      templateUrl: 'app/main/template.html',
      controller: 'MainController',
      controllerAs: 'main'
    });
    return $urlRouterProvider.otherwise('/');
  });

}).call(this);



(function() {
  angular.module('neurology-clinic').controller('MainController', function($rootScope, $scope, $state, $timeout, $interval, $element, $anchorScroll, $location) {
    'ngInject';
    $scope.images = [];
    $scope.clinicImages = ['/assets/images/cli1.jpeg', '/assets/images/cli2.jpeg', '/assets/images/cli3.jpeg', '/assets/images/cli4.jpeg', '/assets/images/cli5.jpeg', '/assets/images/cli6.jpeg'];
    $scope.smImages = ['/assets/images/prof1.jpeg', '/assets/images/prof2.jpeg', '/assets/images/prof3.jpeg', '/assets/images/prof4.jpeg', '/assets/images/prof11.jpeg', '/assets/images/prof13.jpeg', '/assets/images/prof14.jpeg', '/assets/images/prof15.jpeg', '/assets/images/prof17.jpeg'];
    $scope.mdImages = ['/assets/images/prof10.jpeg', '/assets/images/prof16.jpeg', '/assets/images/prof12.jpeg'];
    $scope.lgImages = ['/assets/images/prof5.jpeg', '/assets/images/prof6.jpeg', '/assets/images/prof7.jpeg', '/assets/images/prof8.jpeg', '/assets/images/prof9.jpeg'];
    $scope.methods = {
      getRandomImage: function(array) {
        var image;
        image = $scope.methods.getRan(0, array.length - 1);
        return array[image];
      },
      getRan: function(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
      },
      loadImages: function() {
        $scope.images[1] = $scope.methods.getRandomImage($scope.smImages);
        $scope.images[2] = $scope.methods.getRandomImage($scope.smImages);
        $scope.images[3] = $scope.methods.getRandomImage($scope.mdImages);
        $scope.images[4] = $scope.methods.getRandomImage($scope.lgImages);
        $scope.images[5] = $scope.methods.getRandomImage($scope.mdImages);
        $scope.images[6] = $scope.methods.getRandomImage($scope.smImages);
        return $scope.images[7] = $scope.methods.getRandomImage($scope.smImages);
      },
      changeImageRan: function() {
        var index, ranImg;
        index = $scope.methods.getRan(1, 7);
        if (index === 1 || index === 2 || index === 6 || index === 7) {
          ranImg = $scope.methods.getRandomImage($scope.smImages);
        } else if (index === 3 || index === 5) {
          ranImg = $scope.methods.getRandomImage($scope.mdImages);
        } else {
          ranImg = $scope.methods.getRandomImage($scope.lgImages);
        }
        if ($scope.methods.isAlreadyShowing(ranImg) === -1) {
          return $scope.images[index] = ranImg;
        }
      },
      isAlreadyShowing: function(ranImg) {
        return _.indexOf($scope.images, ranImg);
      },
      scrollToHash: function(hash) {
        if (hash) {
          return $('html, body').animate({
            scrollTop: $("#" + hash).offset().top
          }, 1000, 'easeInOutExpo');
        } else {
          return $('html, body').animate({
            scrollTop: $("html").offset().top
          }, 1000, 'easeInOutExpo');
        }
      }
    };
    $scope.methods.loadImages();
    return $interval($scope.methods.changeImageRan, 1000);
  });

  return;

}).call(this);



